
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import request from '../utils/request'
import { msg, errorMsg } from '../utils/common'
import { mapState } from 'vuex'
export default {
name: 'create',
components: {
Group,
XInput,
Datetime,
XSwitch,
XButton,
PopupPicker
},
data () {
return {
formObj: {
courseName: '', // 
courseType: '', // 
course_date: '', // 
courseStartTime: '', // 
courseEndTime: '', // 
courseInteractive: false, // 01
courseLocation: ''
},
types: ['', '', '', '']
}
},
beforeCreate () {
this.$store.commit('SET_RETURN_TEXT', '')
this.$store.commit('SET_RETURN_URL', '/my')
},
computed: {
...mapState({
teaOpenid: state => state.user.infoObj.teaOpenid,
teaCode: state => state.user.infoObj.teaCode
})
},
methods: {
selType (item) {
this.formObj.courseType = item[0]
},
createCourse () {
const tmpDate = this.formObj.course_date 
const tmpStart = this.formObj.course_date + ' ' + this.formObj.courseStartTime + ':00'
const tmpEnd = this.formObj.course_date + ' ' + this.formObj.courseEndTime + ':00'
const tmpInt = this.formObj.courseInteractive ? 0 : 1
request({
url: 'http://www.zhiheyikaoqin.cn/sdc/tea/createCourse',
method: 'post',
data: {
teaCode: this.teaCode, //  string  
teaOpenid: this.teaOpenid, //  string  
courseType: this.formObj.courseType, // 
courseName: this.formObj.courseName, //   string  
courseDate: tmpDate, //  date  
courseStartTime: tmpStart, //  date  
courseEndTime: tmpEnd, //  date  
courseInteractive: tmpInt, //  int 01
courseLocation: this.formObj.courseLocation 
}
}).then(res => {
if (res.data.code !== 0) {
errorMsg(this, res.data.msg, '120px')
} else {
msg(this, '')
setTimeout(() => {
this.$router.go(-1)
}, 500)
}
})
},
selDate () {
console.log('selDate')

}
}
}



